import React, { Suspense } from 'react'
import { BrowserRouter, Route, Switch } from 'react-router-dom'
import { routes } from './route'

/* Use components to define routes */
const RouterView = () => (
  <BrowserRouter basename="/credits-exchange">
    <Suspense fallback={''}>
      <Switch>
        {routes.map(route => {
          return (
            <Route
              key={route.path}
              path={route.path}
              exact={route.exact}
              component={props => {
                document.title = route?.meta?.title || '腾讯音乐榜'
                return <route.component {...props} routes={route.component} />
              }}></Route>
          )
        })}
      </Switch>
    </Suspense>
  </BrowserRouter>
)
export default RouterView